
import { defineComponent, PropType } from "vue";
import axios, { AxiosResponse } from "axios";

interface Student {
	_id: string;
	name: string;
	class: string;
	cours: {
		_id: string;
		__v: number;
		month: string;
		lessons: {
			_id: string;
			day: string;
			title: string;
			link: string;
		}[];
	}[];
}

interface Util {
	_id: string;
	title: string;
	link: string;
	__v: number;
}

interface DataComponent {
	student: null | Student;
	utils: null | Util[];
}

export default defineComponent({
	name: "Students",

	props: {
		name: {
			type: String as PropType<string>,
			required: true,
			default: "kevin-j",
		},
	},

	data: (): DataComponent => ({
		student: null,
		utils: null,
	}),

	mounted() {
		this.loadStudent();
		this.loadUtils();
	},

	watch: {
		name() {
			this.loadStudent();
		},
	},

	methods: {
		capitalize(str: string): string {
			if (str == "") return "";
			if (str.length === 1) return str.toUpperCase();
			str = str.replaceAll("-", " ");
			return str.charAt(0).toUpperCase() + str.slice(1);
		},
		loadStudent(): void {
			axios
				.get(
					`https://sheltered-basin-99154.herokuapp.com/api/student/${this.name}`
				)
				.then((response: AxiosResponse<{ student: Student }>) => {
					this.student = response.data.student;
					this.student.name = this.capitalize(this.student.name);
				});
		},
		loadUtils(): void {
			axios
				.get(`https://sheltered-basin-99154.herokuapp.com/api/studentUtils`)
				.then((response: AxiosResponse<Util[]>) => {
					this.utils = response.data;
				});
		},
	},
});
